<template>
    <v-app>
        <v-app-bar app dense :elevation="1">
            <v-toolbar-title class="subtitle-1 text-uppercase">
                <span class="title">
                    {{ event ? event.name : '' }}
                    <span
                        class="subtitle-1 ml-2"
                    >{{ poll ? poll.name : '' }}</span>
                </span>
            </v-toolbar-title>
        </v-app-bar>
        <v-content fill-height>
            <v-overlay absolute :value="loading">
                <v-progress-circular indeterminate color="primary"></v-progress-circular>
            </v-overlay>
            <v-overlay absolute :value="status !== 5">
                <v-btn
                    v-if="status === 0 && !loading"
                    color="primary"
                    @click.native="status = 1"
                >comenzar encuesta</v-btn>

                <v-form
                    v-if="status === 1 || status === 3"
                    v-model="valid"
                    ref="form"
                    lazy-validation
                    @keypress.enter.prevent
                >
                    <v-card>
                        <v-overlay absolute :value="dialogOverlay">
                            <v-progress-circular indeterminate color="primary"></v-progress-circular>
                        </v-overlay>
                        <v-card-title primary-title>Cuentanos de ti</v-card-title>
                        <v-card-text>
                            <v-text-field
                                label="E-mail"
                                v-model="user.email"
                                :rules="emailRules"
                                @blur="checkEmail"
                                :disabled="status === 3"
                                required
                            ></v-text-field>
                            <template v-if="status === 3">
                                <v-text-field
                                    label="Name"
                                    v-model="user.name"
                                    :rules="nameRules"
                                    :counter="50"
                                    required
                                ></v-text-field>
                                <v-text-field
                                    label="Apellido"
                                    v-model="user.lastname"
                                    :rules="nameRules"
                                    :counter="50"
                                    required
                                ></v-text-field>
                            </template>
                        </v-card-text>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn text @click="clear">cancelar</v-btn>
                            <v-btn
                                text
                                @click="checkEmail"
                                v-if="status === 1"
                                :disabled="!valid"
                            >verificar</v-btn>
                            <v-btn
                                text
                                @click="submit"
                                v-if="status === 3"
                                :disabled="!valid"
                            >aceptar</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-form>

                <v-card v-if="status === 2">
                    <v-card-title
                        class="text-uppercase headline"
                    >Gracias, {{ user.data.name }}!</v-card-title>
                    <v-card-text>Hemos verificado tu correo. Te invitamos a continuar con la encuesta.</v-card-text>
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn
                            text
                            color="primary"
                            @click.native="status = 5"
                        >aceptar</v-btn>
                    </v-card-actions>
                </v-card>

                <v-card v-if="status === 4">
                    <v-card-title
                        class="text-uppercase headline"
                    >Gracias, {{ user.data.name }}!</v-card-title>
                    <v-card-text>Te invitamos a continuar con la encuesta.</v-card-text>
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn
                            text
                            color="primary"
                            @click.native="status = 5"
                        >aceptar</v-btn>
                    </v-card-actions>
                </v-card>

                <v-card v-if="status === 6">
                    <v-card-title
                        class="text-uppercase headline"
                    >Encuesta Terminada</v-card-title>
                    <v-card-text>Gracias por tomarse el tiempo de completar esta encuesta.</v-card-text>
                </v-card>
            </v-overlay>

            <v-card v-if="!loading">
                <v-form ref="form">
                    <v-card-title
                        primary-title
                    >{{currentQuestion + 1}}. {{ poll.questions[currentQuestion].question }}</v-card-title>
                    <template v-if="poll.questions[currentQuestion].type === 0">
                        <v-card-text>
                            <v-radio-group
                                v-model="poll.questions[currentQuestion].answer.alternative"
                            >
                                <v-radio
                                    v-for="alternative in poll.questions[currentQuestion].alternatives"
                                    :key="alternative.id"
                                    :label="alternative.alternative"
                                    :value="alternative.id"
                                    :disabled="sendingQuestion"
                                    @change="$forceUpdate()"
                                ></v-radio>
                            </v-radio-group>
                        </v-card-text>
                        <v-card-actions>
                            <v-btn
                                color="success"
                                :disabled="currentQuestion === 0 || sendingQuestion"
                                @click="currentQuestion = 0"
                            >
                                <v-icon>mdi-chevron-double-left</v-icon>
                            </v-btn>
                            <v-btn
                                color="success"
                                :disabled="currentQuestion === 0 || sendingQuestion"
                                @click="currentQuestion -= 1"
                            >
                                <v-icon>mdi-chevron-left</v-icon>
                            </v-btn>
                            <!-- <v-btn
                                color="success"
                                :loading="sendingQuestion"
                                :disabled="!poll.questions[currentQuestion].answer.alternative"
                                @click="saveAnswer"
                            >enviar</v-btn>-->
                            <v-btn
                                color="success"
                                v-if="currentQuestion < poll.questions.length"
                                :disabled="!poll.questions[currentQuestion].answer.alternative || sendingQuestion"
                                @click="nextQuestion"
                            >
                                <v-icon>mdi-chevron-right</v-icon>
                            </v-btn>
                            <v-btn
                                color="success"
                                v-if="currentQuestion + 1 >= poll.questions.length"
                                :disabled="!poll.questions[currentQuestion].answer.alternative || sendingQuestion"
                                @click="status = 6"
                            >terminar</v-btn>
                        </v-card-actions>
                    </template>

                    <template v-if="poll.questions[currentQuestion].type === 1">
                        <v-card-text>
                            <v-checkbox
                                v-for="alternative in poll.questions[currentQuestion].alternatives"
                                :key="alternative.id"
                                v-model="poll.questions[currentQuestion].answer.alternatives"
                                :label="alternative.alternative"
                                :value="alternative.id"
                                @change="$forceUpdate()"
                            ></v-checkbox>
                        </v-card-text>
                        <v-card-actions>
                            <v-btn
                                color="success"
                                :disabled="currentQuestion === 0 || sendingQuestion"
                                @click="currentQuestion = 0"
                            >
                                <v-icon>mdi-chevron-double-left</v-icon>
                            </v-btn>
                            <v-btn
                                color="success"
                                :disabled="currentQuestion === 0 || sendingQuestion"
                                @click="currentQuestion -= 1"
                            >
                                <v-icon>mdi-chevron-left</v-icon>
                            </v-btn>
                            <!-- <v-btn
                                color="success"
                                :loading="sendingQuestion"
                                :disabled="poll.questions[currentQuestion].answer.alternatives.length === 0"
                                @click="saveAnswer"
                            >enviar</v-btn>-->
                            <v-btn
                                color="success"
                                v-if="currentQuestion < poll.questions.length"
                                :disabled="poll.questions[currentQuestion].answer.alternatives.length === 0 || sendingQuestion"
                                @click="nextQuestion"
                            >
                                <v-icon>mdi-chevron-right</v-icon>
                            </v-btn>
                            <v-btn
                                color="success"
                                v-if="currentQuestion + 1 >= poll.questions.length"
                                :disabled="poll.questions[currentQuestion].answer.alternatives.length === 0 || sendingQuestion"
                                @click="status = 6"
                            >terminar</v-btn>
                        </v-card-actions>
                    </template>

                    <template v-if="poll.questions[currentQuestion].type === 2">
                        <v-card-text>
                            <v-textarea
                                v-model="poll.questions[currentQuestion].answer.text"
                                label="Respuesta"
                                @change="$forceUpdate()"
                            ></v-textarea>
                        </v-card-text>
                        <v-card-actions>
                            <v-btn
                                color="success"
                                :disabled="currentQuestion === 0 || sendingQuestion"
                                @click="currentQuestion = 0"
                            >
                                <v-icon>mdi-chevron-double-left</v-icon>
                            </v-btn>
                            <v-btn
                                color="success"
                                :disabled="currentQuestion === 0 || sendingQuestion"
                                @click="currentQuestion -= 1"
                            >
                                <v-icon>mdi-chevron-left</v-icon>
                            </v-btn>
                            <!-- <v-btn
                                color="success"
                                :loading="sendingQuestion"
                                :disabled="!this.poll.questions[this.currentQuestion].answer.text"
                                @click="saveAnswer"
                            >enviar</v-btn>-->
                            <v-btn
                                color="success"
                                v-if="currentQuestion + 1 < poll.questions.length"
                                :disabled="!this.poll.questions[this.currentQuestion].answer.text || sendingQuestion"
                                @click="nextQuestion"
                            >
                                <v-icon>mdi-chevron-right</v-icon>
                            </v-btn>
                            <v-btn
                                color="success"
                                v-if="currentQuestion + 1 >= poll.questions.length"
                                :disabled="!this.poll.questions[this.currentQuestion].answer.text || sendingQuestion"
                                @click="nextQuestion(terminar)"
                            >terminar</v-btn>
                        </v-card-actions>
                    </template>
                </v-form>
            </v-card>
        </v-content>

        <v-footer>
            <span>Venoot  2019</span>
        </v-footer>
    </v-app>
</template>

<script>
import { mapState, mapMutations, mapGetters } from 'vuex'
import axios from 'axios'

export default {
    name: 'App',
    props: ['eventid', 'pollid', 'env'],

    data: () => ({
        // eventid: null,
        // pollId: null,
        loading: false,
        sendingQuestion: false,
        status: 0,
        pollOverlay: true,
        valid: false,
        dialogOverlay: false,
        currentQuestion: 0,
        poll: null,
        nameRules: [
            v => !!v || 'Este campo es requerido',
            v => (v && v.length <= 50) || 'Debe ser menos de 30 caracters'
        ],
        emailRules: [
            v => !!v || 'Este campo es requerido',
            v =>
                (!!v &&
                    !!v.match(
                        /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
                    )) ||
                'No es un correo valido'
        ],
        user: {
            name: 'Cristobal',
            lastname: 'Diaz',
            email: 'cristobal@venoot.com'
        }
    }),

    computed: {
        ...mapState(['event']),
        ...mapGetters(['URL'])
    },

    methods: {
        ...mapMutations(['setEvent', 'setEnv']),
        checkEmail() {
            this.dialogOverlay = true
            axios
                .post(`${this.URL}/api/events/${this.eventid}/checkEmail`, {
                    ...this.user,
                    poll_id: this.pollid
                })
                .then(response => {
                    if (response.data.user) {
                        this.status = 2
                        this.user = response.data.user

                        if (response.data.questions) {
                            this.poll.questions = response.data.questions
                            this.poll.questions.forEach(question => {
                                question.answer = {}
                                switch (question.type) {
                                    case 0:
                                        if (question.answers.length > 0) {
                                            question.answer.alternative =
                                                question.answers[0].alternative_id
                                        } else {
                                            question.answer.alternative = null
                                        }
                                        break

                                    case 1:
                                        if (question.answers.length > 0) {
                                            question.answer.alternatives =
                                                question.answers[0].alternative_ids
                                        } else {
                                            question.answer.alternatives = []
                                        }
                                        break

                                    case 2:
                                        if (question.answers.length > 0) {
                                            question.answer.text =
                                                question.answers[0].text
                                        } else {
                                            question.answer.text = ''
                                        }
                                        break
                                }
                            })
                            this.$forceUpdate()
                        }
                    } else {
                        this.status = 3
                    }
                })
                .finally(() => (this.dialogOverlay = false))
        },
        nextQuestion() {
            this.sendingQuestion = true
            axios
                .post(
                    `${this.URL}/api/events/${this.eventid}/polls/${this.pollid}`,
                    {
                        ...this.poll.questions[this.currentQuestion],
                        user_id: this.user.id
                    }
                )
                .then(response => {
                    this.poll.questions[this.currentQuestion].answer.id =
                        response.data.id
                    if (this.currentQuestion + 1 < this.poll.questions.length) {
                        this.currentQuestion++
                    } else {
                        this.status = 6
                    }
                })
                .finally(() => {
                    this.sendingQuestion = false
                    if (this.status !== 6) {
                        currentQuestion += 1
                    }
                })
        },
        clear() {
            this.$refs.form.reset()
            this.dialog = false
        }
    },

    created() {
        this.setEnv(this.env)
        this.loading = true
        axios
            .get(`${this.URL}/api/events/${this.eventid}/polls/${this.pollid}`)
            .then(response => {
                this.poll = response.data.poll
                this.setEvent(response.data.event)
                this.poll.questions.forEach(question => {
                    question.answer = {}
                    switch (question.type) {
                        case 0:
                            question.answer.alternative = null
                            break

                        case 1:
                            question.answer.alternatives = []
                            break

                        case 2:
                            question.answer.text = ''
                            break
                    }
                })
            })
            .catch(error => console.log(error))
            .finally(() => (this.loading = false))
    }
}
</script>

