import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        env: 'production',
        eventId: null,
        pollId: null,
        poll: null,
        event: null
    },

    getters: {
        URL: state => {
            switch (state.env) {
                case 'local':
                    return 'http://localhost:8000'

                case 'staging':
                    return 'https://venoot-stage.work'

                case 'production':
                    return 'https://venoot.com'
            }
        }
    },

    mutations: {
        setEventId(state, id) {
            state.eventId = id
        },

        setEvent(state, event) {
            state.event = event
        },

        setEnv(state, env) {
            state.env = env
        }
    },
    actions: {}
})
